<template>
  <div id="app" class="container-fluid px-0">
    <div
      class="col-12 nav flex-x-between"
      :class="{ onScroll: !view.topOfPage }"
    >
      <img
        :src="isDark ? '/dark_logo.png' : '/light_logo.png'"
        class="logo"
        alt=""
      />
    </div>
    <router-view
      :key="$route.fullPath"
      :isDark="isDark"
      class="router_view mx-0 w-100"
    >
    </router-view>
    <themeToggler @toggle="changeTheme" />
    <slide-y-down-transition>
      <button
        class="no-width scroll-to-top"
        v-if="showScrollToTop"
        @click="scrollToTop"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          stroke-width="2"
          stroke-linecap="round"
          stroke-linejoin="round"
          class="feather feather-chevron-up"
        >
          <polyline points="18 15 12 9 6 15"></polyline>
        </svg>
      </button>
    </slide-y-down-transition>
  </div>
</template>

<script>
import themeToggler from "./components/toggleTheme.vue";

export default {
  data() {
    return {
      showScrollToTop: false,
      isDark: false,
      view: {
        topOfPage: true,
      },
    };
  },
  computed: {},
  mounted() {},
  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
  watch: {},
  components: {
    themeToggler,
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    changeTheme(mode) {
      this.isDark = mode;
    },
    handleScroll(e) {
      if (window.pageYOffset > 0) {
        if (this.view.topOfPage) this.view.topOfPage = false;
      } else {
        if (!this.view.topOfPage) this.view.topOfPage = true;
      }

      this.showScrollToTop = e.target.scrollingElement.scrollTop > 500;
    },
    scrollToTop() {
      window.scrollTo(0, 0);
    },
  },
};
</script>
<style>
